%text-reset {
  font-family: inherit;
  font-size: inherit;
  margin: 0;
}

%list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

%link-reset {
  color: inherit;
  text-decoration: none;
}

%space-between-elemets {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
